body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: url("/assets/Images/Vertical_mid_lane.jpg"); */
  /* cursor: url(/assets/Images/blitz_hand.png), auto; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    z-index: 99;
    min-height: 45px;
    background:#004e66;
    position: fixed;
    top: 0;
    width: 100%;
    white-space: nowrap;
    box-shadow: 0 14px 28px rgba(49, 180, 255, 0.25), 0 10px 10px rgba(32, 248, 86, 0.22);
}
.navbar ul{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-left: 0px;
}
.navbar li {
    width: 33%;
    box-sizing: border-box;
    padding:0px 10px;
    text-align: center;
    display: inline-block;
}
.navbar a {
    
    color:#fcbe32;
    font-weight: 900;
    text-decoration: none;
    margin-left: 40px;
  }
.score{
    color:#00dffc;
    
}
.top{
    color:#f1404c;
}
.blitzIcon{
    
    height: 50px;
    width: 50px;
    display: block;
    position: absolute;
    
    left: 0;
    top: 0;
    padding: auto;
    
    z-index: -1;
}
.gameTitle {
    display: flex;
    flex-direction: row;
}
.titleSpan {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.difficulty {
    width: 50px;
    height: 50px;
    margin:-16px 0px;
    justify-self: flex-end;
}
.ez:hover{
    cursor: url("https://rch92.github.io/React_game_LOL//assets/Images/easy_poro64.png") 25 15, auto;
    border-radius: 50%;
    background:#00dffc;
}
.hard:hover{
    cursor: url("https://rch92.github.io/React_game_LOL//assets/Images/hard_poro64.png") 25 15, auto;
    border-radius: 50%;
    background:#f1404c;
    
}
#difficultyBox {
     display: flex;
    flex-direction: row;
    justify-content: flex-end;
    
}
.instructions {
    background-color: #e1eef6;
    box-sizing: border-box;
    display: flex;
    flex-direction: row; 
    justify-content: space-between;
    align-items: center;
    /* background-image: url("/assets/images/Blitz_animated.gif"); */
    /* background-image: url("/assets/images/TF_banner.png"); */
    background-image: url("https://rch92.github.io/React_game_LOL//assets/Images/TF_highres.png");
    min-height: 80px;
    max-height: 250px;
    /* background-size: 250px; */
    background-size: auto 230px;
    background-repeat: no-repeat;
    
    padding: 60px 20px; 
    background-position: bottom right;
    box-shadow: 0 14px 28px rgba(14, 14, 14, 0.25), 0 10px 10px rgba(8, 8, 8, 0.22);
    z-index: 50;
    margin-top: 50px;
    
}
.instructions h1 {
    text-align: center;
}
.instructions h2 {
    text-align: center;
}
.instructions #tfEther {
        box-sizing: inherit;
        
        width: 225px;
       
        /* height: 240ex; */
        /* height: 80%; */
        /* position: inherit; */
        /* transform: translate(0px 50%); */
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        -webkit-filter: FlipH;
                filter: FlipH;
        -ms-filter: "FlipH";
        z-index: 1;
        

}
#spacer {
    width: 225px; 
}
.instructionText {
    padding: inherit;
    padding-left: 10px;
    padding-right: 10px;
    background: azure;
}
.CardDiv {
    background-image: url("https://rch92.github.io/React_game_LOL/assets/Images/Vertical_mid_lane.jpg");
    background-size: cover;
    overflow:hidden;
    height:1200px;
    
    
}
.wrapper{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-items: center;
    justify-content: space-evenly;
    max-width: 800px;
    margin: auto;
    background: transparent;
    -webkit-padding-before: 150px;
            padding-block-start: 150px;
}
.blitzCrank{
    display: none;
}
@media (min-width: 1200px){
    .blitzCrank{
        display: unset;
        -webkit-transform: translateX(40px);
                transform: translateX(40px);
        position: absolute;
        width: 200px;
        height: 200px;
        margin: auto;
        overflow: hidden;
        z-index: 1;
        /* margin-inline-start: 0px -100px; */
        pointer-events: none;

    }
}
.card {
    width: 100px;
    height: 150px;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 10px 10px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
.card:hover {
    
    box-shadow: 0px 0px 80px rgba(250, 203, 47, 0.561), 0 0px 15px rgba(231, 255, 20, 0.555);
    transition: 0.1s;
    cursor: url("https://rch92.github.io/React_game_LOL//assets/Images/blitz_hand_square64.png"), auto;
  }
  
